* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #1a1a1c;
  color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.coin-app {
  align-items: center;
  margin-top: 64px;
  color: #fff;
}
.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.coin-text {
  margin-bottom: 32px;
  text-align: center;
}
.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border: none;
  border-radius: 4px;
  background-image: linear-gradient(
    -225deg,
    #ac32ef 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: #e2e2e2;
  font-size: 18px;
}

.coin-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.coin-row {
  display: flex;
  flex-direction: row;
  justify-items: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  width: 900px;
}
.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}

.coin h1 {
  font-size: 16px;
  width: 150px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}
.coin-symbol {
  text-transform: uppercase;
}

.coin-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
}
.coin-price {
  width: 110px;
}

.coin-volume {
  width: 230px;
}
.coin-marketcap {
  width: 155px;
}
.coin-percent {
  width: 100%;
}
.red {
  color: #f00606;
}
.green {
  color: #11d811;
}
